import { BehaviorSubject } from 'rxjs';
import { Injectable } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Parse } from 'parse';
import { Router } from '@angular/router';
@Injectable({
  providedIn: 'root'
})
export class AuthService {

  public isAuthenticated = new BehaviorSubject<boolean>(false);

  constructor(
    private router: Router,
    private snackBar: MatSnackBar
  ) {
  }

  async checkAuthenticated() {
    const authenticated = await Parse.User.current();
    this.isAuthenticated.next(authenticated);
    return authenticated;
  }

  async getUser() {
    return await Parse.User.current();
  }

  async login(username: string, password: string) {
    const transaction = await Parse.User.logIn(username, password);

    if (transaction) {
      this.router.navigate(['/home']);
      this.snackBar.open('Login Successful', '', {
        duration: 2000,
      });
      this.isAuthenticated.next(true);
    } else {
      this.snackBar.open('Invalid Credentials, Please try again', '', {
        duration: 2000,
        panelClass: 'warn'
      });
    }
  }

  async register(email: string, username: string, password: string) {
    const user = new Parse.User();
    user.set('username', username);
    user.set('password', password);
    user.set('email', email);

    try {
      await user.signUp();
      this.router.navigate(['/']);
      this.snackBar.open('Registration Successful', '', {
        duration: 2000,
      });
    } catch (error) {
      this.snackBar.open(error, '', {
        duration: 2000,
      });
    }
  }

  async logout(redirect: string) {
    if (this.isAuthenticated) {
      await Parse.User.logOut();
      this.isAuthenticated.next(false);
      this.router.navigate([redirect]);
      this.snackBar.open('Logout Successful', '', {
        duration: 2000,
      });
    }
  }
}
