import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Idea } from '../../idea.interface';

@Component({
  selector: 'app-new-idea-dialog',
  templateUrl: './new-idea-dialog.component.html',
  styleUrls: ['./new-idea-dialog.component.scss']
})
export class NewIdeaDialogComponent {

  constructor(
    public dialogRef: MatDialogRef<NewIdeaDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Idea
  ) { }

  cancel(): void {
    this.dialogRef.close();
  }

  save(): void {
    this.dialogRef.close(this.data);
  }

}
