import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { animate, state, style, transition, trigger } from '@angular/animations';
import { catchError, map, startWith, switchMap } from 'rxjs/operators';
import { merge, of as observableOf } from 'rxjs';

import { AuthService } from '../auth.service';
import { FormControl } from '@angular/forms';
import { Idea } from '../idea.interface';
import { IdeaService } from '../idea.service';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { NewIdeaDialogComponent } from './new-idea-dialog/new-idea-dialog.component';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { ThemePalette } from '@angular/material/core';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({ height: '0px', minHeight: '0' })),
      state('expanded', style({ height: '*' })),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})
export class HomeComponent implements AfterViewInit, OnInit {
  columnsToDisplay: string[] = ['title', 'upVotes', 'downVotes', 'createdAt', 'updatedAt', 'createdBy', 'action'];
  dataSource: MatTableDataSource<Idea>;
  expanded: Idea | null;
  data: Idea[] = [];
  user;
  filterText: string;

  color: ThemePalette = 'accent';
  mode: ProgressSpinnerMode = 'indeterminate';

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  resultsLength = 0;
  isLoadingResults = true;
  searchField: FormControl;

  constructor(
    public dialog: MatDialog,
    private ideaService: IdeaService,
    private authService: AuthService
  ) {

  }

  ngOnInit() {
    this.authService.getUser().then(u => this.user = u);
    this.searchField = new FormControl();
  }

  ngAfterViewInit() {
    this.sort.sortChange.subscribe(() => this.paginator.pageIndex = 0);
    const filterChange = this.searchField.valueChanges.pipe(
      debounceTime(1000),
      distinctUntilChanged()
    );
    merge(this.sort.sortChange,
      this.paginator.page,
      filterChange)
      .pipe(
        startWith({}),
        switchMap(async () => {
          // if (this.isLoadingResults) { return; }
          this.isLoadingResults = true;
          //this.resultsLength = await this.ideaService.getIdeasCount();
          const resp = await this.ideaService.getIdeas(
            this.paginator.pageIndex * this.paginator.pageSize,
            this.paginator.pageSize,
            this.sort.active,
            this.sort.direction,
            this.filterText || ''
          );
          this.resultsLength = resp.count;
          return resp.results;
        }),
        map(data => {
          // Flip flag to show that loading has finished.
          this.isLoadingResults = false;
          return data;
        }),
        catchError(() => {
          this.isLoadingResults = false;
          return observableOf([]);
        })
      ).subscribe(data => {
        // if (!data) { return; }
        this.data = data.map(d => {
          console.log(d);
          return Object.assign({}, d.attributes,
            { id: d.id },
            { createdAt: d.createdAt },
            { updatedAt: d.updatedAt },
            { canDelete: d.attributes.createdBy.id === this.user.id ? true : false });
        });
      });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  async toggleVote(idea: Idea, type: number) {
    this.isLoadingResults = true;
    try {
      await this.ideaService.updateVotes(idea.id, type);
      idea[type ? 'upVotes' : 'downVotes'] += 1;
      this.isLoadingResults = false;
    } catch (e) {
      console.error(e);
      this.isLoadingResults = false;
    }
  }

  openDialog(): void {
    const noIdea: Idea = { title: '', description: '' };
    const dialogRef = this.dialog.open(NewIdeaDialogComponent, {
      data: noIdea
    });

    dialogRef.beforeClosed().subscribe(result => {

      if (!result) { return; }
      this.isLoadingResults = true;
      const idea: Idea = {
        title: result.title,
        description: result.description
      };

      this.ideaService.createIdea(idea).then(() => {
        this.isLoadingResults = false;
        // reload the table
        this.paginator._changePageSize(this.paginator.pageSize);
      }).catch((e) => {
        console.log(e);
        this.isLoadingResults = false;
      });

    });
  }

  async removeIdea(idea: Idea) {
    this.isLoadingResults = true;
    try {
      await this.ideaService.deleteIdea(idea.id);
      // reload the table
      this.paginator._changePageSize(this.paginator.pageSize);
    } catch (e) {
      console.error(e);
      this.isLoadingResults = false;
    }
  }

}
