import { Idea } from './idea.interface';
import { Injectable } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Parse } from 'parse';

@Injectable({
  providedIn: 'root'
})
export class IdeaService {

  constructor(
    private snackBar: MatSnackBar
  ) { }


  async createIdea(newIdea: Idea) {

    const ideas = Parse.Object.extend('ideas');
    const idea = new ideas(newIdea);

    idea.set('upVotes', 0);
    idea.set('downVotes', 0);
    idea.set('createdBy', Parse.User.current()
    );

    try {
      const saveStatus = await idea.save();
      this.snackBar.open('Idea Created Successfully', '', {
        duration: 2000,
      });
      return saveStatus;
    } catch (error) {
      this.snackBar.open(error, '', {
        duration: 5000,
      });

      return false;
    }
  }

  async getIdeasCount() {
    const Ideas = Parse.Object.extend('ideas');
    const query = new Parse.Query(Ideas);
    return await query.count();
  }

  async getIdeas(skip: number,
    limit: number,
    field: string,
    order: string,
    filterText: string) {
    const Ideas = Parse.Object.extend('ideas');
    let query = new Parse.Query(Ideas);

    query.limit(limit); // limit to at most 10 results
    query.skip(skip); // skip the first 10 results
    query.include('createdBy');
    if (order === 'asc') {
      query.ascending(field);
    } else {
      query.descending(field);
    }
    if (filterText) {
      query.fullText('description', filterText);

      // const filterDescription = new Parse.Query(Ideas);
      // filterDescription.fullText('description', filterText);

      // query = Parse.Query.or(query, filterDescription);
    }
    query._count = true;
    const results = await query.find();

    return results;
  }

  async updateVotes(id: string, type: number) {
    const Ideas = Parse.Object.extend('ideas');
    const query = new Parse.Query(Ideas);

    const idea = await query.get(id);
    idea.increment(type ? 'upVotes' : 'downVotes', 1);
    try {
      const updStatus = await idea.save();
      this.snackBar.open(`${type ? 'Up Vote' : 'Down Vote'} Saved Successfully`, '', {
        duration: 2000,
      });
      return updStatus;
    } catch (error) {
      this.snackBar.open(error, '', {
        duration: 5000,
      });
      return false;
    }
  }

  async deleteIdea(id: string) {
    const Ideas = Parse.Object.extend('ideas');
    const query = new Parse.Query(Ideas);

    const idea = await query.get(id);
    try {
      const delStatus = await idea.destroy();
      this.snackBar.open(`Idea Deleted Successfully`, '', {
        duration: 2000,
      });
      return delStatus;
    } catch (error) {
      this.snackBar.open(error, '', {
        duration: 5000,
      });
      return false;
    }
  }

}
